export default {
	name:"KButton",
	props:{
		 /* 内置按钮风格 */
		type: {//按钮状态
			type: String,
			default: 'normal'
		},
		// text: {//文字
		// 	type: String,
			
		// },
		plain:{//朴素按钮
			type: Boolean,
		},
		round:{//圆角
			type: Boolean,
		},
		disabled:{//可选
			type: Boolean,
		},
		size:{//尺寸small.medium.large
			type: String,
			default: 'small'
		},
		block:{//可选
			type: Boolean,
		},
		icon:{//图标
			type: String,
		},
		icon_rotate:{
			type: Boolean,
		}
	},
	computed: {
		/* 根据不同的props，展示按钮样式 */
		styles: {
			get() {
				return ['sp-button',  this.type, 
				this.plain?'plain':'',this.size,
				this.round?'round':'',
				this.disabled?'disabled':'',
				this.block?'block':'',
				]
			}
		},
		icon_styles:{
			get(){
				return['fa',this.icon,
				this.icon_rotate?'icon_rotate':'',
				]
			}
		}
	},
	data() {
		return {
		};
	},
	mounted() {
	},
	watch:{
	},
	methods:{
		_click(){
			this.$emit('click')
			// console.log('_a')
		}
	}
}
